.App {
  margin: 0;
  padding: 0  ;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100%;
  background-color: #1D2025;
  position: absolute;
}

@media screen and (max-width:600px) {
  .App {
    height: inherit;
  }
}